.Toolbar {
  height: 56px;
  width: 100%;
  z-index: 90;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #fd9e5e;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 20px;
}

.Toolbar nav {
  height: 100%;

}

.Logo {
  height: 90%;
}

@media (max-width: 499px) {
  .DesktopOnly {
    display: none;
  }

  .Logo {
    display: none;
  }
}


