.NavigationItem {
  margin: 10px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  width: 100%;
}

.NavigationItem a {
  text-decoration: none;
  width: 100%;
  display: block;
  color: hsl(0, 66%, 61%);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-weight: 600;
  white-space: nowrap;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
  color: #35aaba;
  font-weight: bold;
}

@media (min-width: 500px) {
  .NavigationItem {
    margin: 0;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    width: auto;
    -ms-flex-align: center;
        align-items: center;
  }
  
  .NavigationItem a {
    color: white;
    padding: 16px 8px;
    height: 100%;
    border-bottom: 4px solid transparent;
  }
  
  .NavigationItem a:hover,
  .NavigationItem a:active,
  .NavigationItem a.active {
    background-color: #f78234;
    border-bottom: 4px solid #2B4570;
    color: #2B4570;
    font-weight: bold;
  }
}


