.Menu {
  margin: 10px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%;
  width: 100%;
  padding: 5px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.Menu img {
  height: 80%
}
.Menu img:first-child {
  margin-right: auto;
  cursor: pointer;
}

@media (min-width: 500px) {
  .Menu {
     display: none;
  }
}


